import './App.css';
import React, {useState, useEffect} from 'react';

function App() {
  let [hora, setHora] = useState(0);
  let [minuto, setMinuto] = useState(0);
  let [segundo, setSegundo] = useState(0);
  let enable = false;

  function buttonStart(){
    enable = true;
  }
  useEffect(() => {
    
    const interval = setInterval(()=>{
     
       setSegundo(segundo+1)
    

      if(segundo === 59){
        setSegundo(0);
        setMinuto(minuto+1)
      }
      if(minuto === 59){
        setMinuto(0);
        setHora(hora+1)
      }
      

    },1000);

    return () => clearInterval(interval);
  })

  let FormatHora = hora;
  let FormatMinuto = minuto;
  let FormatSegundo = segundo;

  if(segundo < 10){
    FormatSegundo = '0' + segundo;
  }if(minuto < 10){
    FormatMinuto = '0' + minuto;
  }if(hora < 10){
    FormatHora = '0' + hora;
  }

  

  return (
      <div className="App">
        <h2>{FormatHora}:{FormatMinuto}:{FormatSegundo}</h2>
        <button onClick={()=>buttonStart()}>Iniciar cron√¥metro</button>
      </div>
  );
}

export default App;
